.skills {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

.skills__item {
	margin-bottom: 35px;
	width: 25%;

	@media (max-width: 480px) {
		width: 130px;
	}
}

.skills__checkbox {
	@include visually-hidden;
}

.skills__label {
	font-size: 12px;
	text-transform: uppercase;

	&:hover,
	&:focus {
		color: $color-blue;
	}
}

.skills__checkbox + .skills__label::before {
	content: "";
	display: inline-block;
	vertical-align: middle;
	margin-right: 10px;
	border-radius: 5px;
	width: 19px;
	height: 19px;
	background-color: $color-grey;
	transition: 0.5s;
}

.skills__checkbox:checked + .skills__label::before {
	background: $color-blue url("../images/check.svg") no-repeat center;
}

.skills__checkbox:hover + .skills__label::before,
.skills__checkbox:focus + .skills__label::before {
	box-shadow: 0 0 3px 1px rgba($color-text, 0.2);
	transform: scale(1.2);
}
