.result {
	display: flex;
	flex-wrap: wrap;
	margin: 0 auto;
	padding-top: 58px;
	padding-bottom: 17px;
	width: 660px;

	> div {
		width: 50%;
	}

	@media (max-width: 720px) {
		justify-content: center;
		width: 95%;

		> div {
			width: auto;
		}
	}
}

.scale {
	position: relative;
	display: flex;
	flex-wrap: wrap;
}

.scale__slice {
	&:hover,
	&:focus {
		transform: scale(1.1);
	}
}

.scale__slice--yellow,
.scale__slice--blue {
	margin-top: 28px;
	width: 83px;
}

.scale__slice--yellow {
	margin-right: -25px;
}

.scale__slice--blue {
	margin-left: -27px;
}

.scale__slice--green {
	width: 146px;
}

#arrow {
	position: absolute;
	left: 83px;
	bottom: 50px;
	width: 57px;
	height: 39px;
	transition: 0.8s linear;

	&.yellow {
		transform: rotate3d(0, 0, 1, -20deg) translate(0, 3px);
	}

	&.green {
		transform: rotate3d(0, 0, -2, -56deg) translate(3px, -18px);
	}

	&.blue {
		transform: rotate3d(0, 0, -2, -125deg) translate(-18px, -31px);
	}
}

.scale-result__title {
	margin: 8px 11px 12px;
	max-width: 200px;
	line-height: 1.3;
}

.result__number {
	margin: 0 11px;
	font-weight: 900;
	font-size: 50px;
	color: $color-blue;
}

@media (max-width: 550px) {
	.result__text {
		text-align: center;
	}
}
