.main-block {
	padding: 60px 0;
	min-height: 100vh;
	font-family: $font;
	font-weight: 300;
	color: $color-text;
	background-color: #f0f0f0;
}

.form {
	margin: 0 auto;
	width: 660px;
	min-width: 300px;

	@media (max-width: 720px) {
		width: 95%;
	}
}

.form-block {
	border-bottom: 2px solid #f0f0f0;
	padding: 0 20px;
	background: url("../images/background.jpg") no-repeat center/cover;
}

.form__contacts {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	align-items: flex-start;
	padding-top: 20px;
	padding-bottom: 10px;

	&::after {
		content: "";
		position: absolute;
		right: 0;
		bottom: 8px;
		width: 184px;
		height: 166px;
		background: url("../images/stamp.svg") no-repeat right bottom/contain;
	}

	@media (max-width: 480px) {
		justify-content: center;

		&::after {
			width: 105px;
			height: 85px;
		}
	}
}

.form__profile {
	flex-grow: 1;
	margin-left: 24px;

	@media (max-width: 480px) {
		margin: 10px auto;
		width: 95%;
	}
}

.form__input {
	display: block;
	margin-bottom: 23px;
	border: none;
	border-bottom: 2px solid #333;
	padding: 7px 0;
	width: 100%;
	height: 32px;
	font-size: 12px;
	text-transform: uppercase;
	background: transparent;
	transition: 0.5s;

	&::placeholder {
		text-transform: uppercase;
		color: $color-text;
	}

	&:not(:disabled):hover,
	&:not(:disabled):active {
		border-color: $color-blue;
	}

	&:not(:disabled):focus {
		outline: none;
		border-color: $color-blue;
		border-top: none;
		border-right: none;
		border-left: none;
	}

	&:disabled {
		border-color: $color-text;
		background: $color-grey;
	}
}

.form__skills {
	padding-top: 35px;
}

.form__title {
	margin-top: 0;
	margin-bottom: 15px;
	font-size: 18px;
	color: $color-title;
}
